import * as React from 'react';
import { insertTable } from '../../actions';
import type { MobileToolbarButtonProps } from '@ali/4ever-component';

export function MobileTableButton(props: MobileToolbarButtonProps) {
  const { controller, item, Wrapper, locale } = props;
  const isDisabled = !controller.query('canInsertTable');

  const handleClick = React.useCallback(
    () => controller.run('onAction', insertTable(3, 3)),
    [controller],
  );

  return (
    <Wrapper
      item={item}
      type="line"
      icon="table"
      lineText={locale?.quickInsertTitle}
      isDisabled={isDisabled}
      onClick={handleClick}
    />
  );
}
